  box-shadow: 0 4px 10px 0 #fff;
  position: absolute;
  top: 50%;
  left: 60%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.248);
}

.slider__item_title {
